import { request } from './request'
import qs from "qs";

//
export function login(data) {
    return request({
        method: 'post',
        url: '/robot/token/login',
        data: data
    })
}

//
export function check(data) {
    return request({
        method: 'post',
        url: '/robot/token/register',
        data: data
    })
}


export let submitJdCk = (params) => {
    return request({
        methods: 'get',
        url: '/robot/jdCk/addCk?ck=' + encodeURIComponent(`${params}`),
    })
}

export let getJdInfo = (params) => {
    return request({
        methods: 'get',
        url: `/robot/jdHelp/getJdInfo`,
    })
}

//
//
export function queryQl(data) {
    return request({
        method: 'get',
        url: '/robot/ql/queryQls',
        params: qs.stringify(data)
        // params: data
    })
}

